body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.selectInput {
}

.selectInput label {
  font-size: 1rem;
  font-weight: bold;
}

.selectInput select,
.selectInput input[type="text"] {
  margin: 1rem 0;
  background-color: white;
  border: 1px solid #58595b;
  border-bottom: 2px solid #58595b;
  border-radius: 0;
  height: 3rem;
  padding: 10px 20px 10px 10px;
}

.answerInput {
  display: flex;
  flex-direction: column;
}

.answerInput label {
  font-size: 1rem;
  font-weight: bold;
}

.answerInput input[type="text"],
.answerInput input[type="email"],
.answerInput input[type="number"],
.answerInput input[type="password"] {
  margin: 1rem 0;
  background-color: white;
  border: 1px solid #58595b;
  border-bottom: 2px solid #58595b;
  border-radius: 0;
  height: 3rem;
  padding: 10px 20px 10px 10px;
}

.left-side {
  flex: 1 1;
}

.left-side .left-side__container {
  width: 70%;
}

.right-side {
  flex: 1 1;
  display: flex;
  justify-content: flex-end;
}

.right-side .right-side__container {
  padding: 1.5rem;
  border: 1px solid #58595b;
  border-radius: 5px;
  /* width: 70%; */
  align-self: flex-end;
}

.right-side h4 {
  font-size: 1.2rem;
  color: #007b5e;
}

.sides {
  padding: 0 2rem;
}

.sides h3 {
  font-size: 2rem;
  color: #007b5e;
}

.sides .sides-container {
  display: flex;
  align-items: flex-start;
}

.button-container {
  width: 100%;
  margin: 1rem 0 4rem 0;
  padding: 0 2rem;
  box-sizing: border-box;
}

.button-container .button {
  height: 3rem;
  border: none;
  border-bottom: 2px solid #944f37;
  width: 15%;
}

.button-container .link {
  display: flex;
  align-items: center;
  margin: 1.5rem 0;
}

.button-container .link a {
  color: #58595b;
}

.button-container .link span {
  height: 1rem;
  border-right: 2px solid #58595b;
  margin: 0 1rem;
}

.steps {
  z-index: 999;
  color: #58595b;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem 0;
}

.steps .step {
  margin: 0 10px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.steps .step p {
  padding: 0;
  margin: 0;
}

.steps .step p:nth-child(2) {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30px;
  width: 30px;
  border: 1px solid #58595b;
  border-radius: 100%;
  margin-top: 10px;
}

.steps .step.active {
  color: #007b5e;
}
.steps .step.active p {
  font-weight: 600;
}
.steps .step.active p span {
  font-weight: 600;
}

.steps .step.active p:nth-child(2) {
  color: white;
  border: 1px solid #007b5e;
  background-color: #007b5e;
}

.successful {
  padding: 0 2rem;
  padding-bottom: 10rem;
}

.successful h3 {
  color: white;
  font-size: 2rem;
  background-color: #007b5e;
  padding: 1rem;
  width: 100%;
}

.successful p {
  font-size: 1.5rem;
  padding: 0 1rem;
  color: #82c25d;
}

