html:not(.lt-ie9) .checkbox-item input[type="checkbox"] {
  position: absolute;
  opacity: 0;
}
html:not(.lt-ie9) .checkbox-item input[type="checkbox"] + label,
html:not(.lt-ie9)
  .checkbox-item
  input[type="checkbox"]
  + span.inline-tooltip
  > label {
  padding-left: 22px;
  display: block;
  position: relative;
}
html:not(.lt-ie9) .checkbox-item input[type="checkbox"] + label:before,
html:not(.lt-ie9)
  .checkbox-item
  input[type="checkbox"]
  + span.inline-tooltip
  > label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 2px;
  width: 15px;
  height: 15px;
  border: 1px solid #b9bbbd;
  border-radius: 2px;
}
html:not(.lt-ie9) .checkbox-item input[type="checkbox"]:focus + label:before,
html:not(.lt-ie9)
  .checkbox-item
  input[type="checkbox"]:focus
  + span.inline-tooltip
  > label:before {
  box-shadow: 0 0 5px #007b5e !important;
  border-color: #007b5e;
}
html:not(.lt-ie9) .checkbox-item input[type="checkbox"]:checked + label:after,
html:not(.lt-ie9)
  .checkbox-item
  input[type="checkbox"]:checked
  + span.inline-tooltip
  > label:after {
  position: absolute;
  font-family: "citi-icons";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  left: 0px;
  top: 1px;
  content: "\e62a";
