/* ==========================================================================
 Helper classes
 ========================================================================== */
/*
* showhide-content
* content connected to a control with class "showhide"
*/
.showhide-content {
  display: none;
}

/*
* Top Shadow
*/
.topshadow {
  height: 8px;
  margin: 0 0 30px 0;
  background: radial-gradient(
    farthest-side at 50% 100%,
    rgba(0, 0, 0, 0.1),
    rgba(0, 0, 0, 0) 100%
  );
  background-repeat: no-repeat;
  background-size: cover;
}
/*
* bottom Shadow
*/
.bottomshadow {
  height: 6px;
  margin: 0;
  background: radial-gradient(
    farthest-side at 50% 0%,
    rgba(0, 0, 0, 0.1),
    rgba(0, 0, 0, 0) 100%
  );
  background-repeat: no-repeat;
  background-size: cover;
}

.lt-ie10 .topshadow {
  border-bottom: 1px solid #dcddde;
}

/*
* Drop Shadow
*/
.dropshadow {
  height: 5px;
  margin-bottom: -5px;
  background: radial-gradient(
    farthest-side at 50% 0%,
    rgba(0, 0, 0, 0.1),
    rgba(0, 0, 0, 0) 100%
  );
  background-repeat: no-repeat;
  background-size: cover;
}

/*
* Image replacement
*/

.ir {
  background-color: transparent;
  border: 0;
  overflow: hidden;
  /* IE 6/7 fallback */
  *text-indent: -9999px;
}

.ir:before {
  content: "";
  display: block;
  width: 0;
  height: 150%;
}

/*
* Hide from both screenreaders and browsers: h1bp.com/u
*/

.hidden {
  display: none !important;
  visibility: hidden;
}

/*
* Hide only visually, but have it available for screenreaders: h1bp.com/v
*/

.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

/*
* Extends the .visuallyhidden class to allow the element to be focusable
* when navigated to via the keyboard: h1bp.com/p
*/

.visuallyhidden.focusable:active,
.visuallyhidden.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

/*
* Hide visually and from screenreaders, but maintain layout
*/

.invisible {
  visibility: hidden;
}

/*
* Clearfix: contain floats
*
* For modern browsers
* 1. The space content is one way to avoid an Opera bug when the
*    `contenteditable` attribute is included anywhere else in the document.
*    Otherwise it causes space to appear at the top and bottom of elements
*    that receive the `clearfix` class.
* 2. The use of `table` rather than `block` is only necessary if using
*    `:before` to contain the top-margins of child elements.
*/

.clearfix:before,
.clearfix:after {
  content: " "; /* 1 */
  display: table; /* 2 */
}

.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;
}

/*
* Alignment classes
*/
.left {
  float: left;
}
.right {
  float: right;
}
/*
* For the Tank Buildkit
* Ensure tools do not appear until fully loaded
*/

.buildkit-actions {
  display: none;
}

/*
For Tab Order issue in RSA Challenge. Remember Me was getting skipped 
*/
#cbRegister,
#cbSaveUserID {
  visibility: visible;
}

